.postBlock{
    padding: 10px;
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 1px 0 0 #d3d9de, 0 0 0 1px #e7e8ec;
    margin: 15px 15px 0 0;
}
.posts{
    margin-top: 10px;
}
.postText div div textarea{
    border: none;
    border-bottom: solid 1px #ccc;
    width:100%;
}
.addPostBtn {
    background: #5181b8;
    color: #fff;
    border: none;
    padding: 10px;
    cursor: pointer;
}
textarea:focus, input:focus{
    outline: none;
}